import Vue from 'vue'
import VueRouter from 'vue-router'
import iroot from '../root/root.config.js'
Vue.use(VueRouter)
// 0.   Vue.use(VueRouter)

// 1. 
//  import 
const Home = resolve => {
    require.ensure(['./../../src/components/Home.vue'], () => {
        resolve(require('./../../src/components/Home.vue'))
    })
}

const WebStorage = resolve => {
    require.ensure(['./../../src/components/WebStorage.vue'], () => {
        resolve(require('./../../src/components/WebStorage.vue'))
    })
}

const History = resolve => {
    require.ensure(['./../../src/components/History.vue'], () => {
        resolve(require('./../../src/components/History.vue'))
    })
}

const WebWorkers = resolve => {
    require.ensure(['./../../src/components/WebWorkers.vue'], () => {
        resolve(require('./../../src/components/WebWorkers.vue'))
    })
}

const DragAndDrop = resolve => {
    require.ensure(['./../../src/components/DragAndDrop.vue'], () => {
        resolve(require('./../../src/components/DragAndDrop.vue'))
    })
}

const File = resolve => {
    require.ensure(['./../../src/components/File.vue'], () => {
        resolve(require('./../../src/components/File.vue'))
    })
}

const Geolocation = resolve => {
    require.ensure(['./../../src/components/Geolocation.vue'], () => {
        resolve(require('./../../src/components/Geolocation.vue'))
    })
}

const Audio = resolve => {
    require.ensure(['./../../src/components/Audio.vue'], () => {
        resolve(require('./../../src/components/Audio.vue'))
    })
}

const Video = resolve => {
    require.ensure(['./../../src/components/Video.vue'], () => {
        resolve(require('./../../src/components/Video.vue'))
    })
}

const Canvas2d = resolve => {
    require.ensure(['./../../src/components/Canvas2d.vue'], () => {
        resolve(require('./../../src/components/Canvas2d.vue'))
    })
}

const Cache = resolve => {
    require.ensure(['./../../src/components/Cache.vue'], () => {
        resolve(require('./../../src/components/Cache.vue'))
    })
}

const IndexedDB = resolve => {
    require.ensure(['./../../src/components/IndexedDB.vue'], () => {
        resolve(require('./../../src/components/IndexedDB.vue'))
    })
}

// 2. 
//  "component" 
//  Vue.extend() 
// 
// 
const routes = [
    { path: `${iroot}home`, component: Home },
    { path: `${iroot}webstorage`, component: WebStorage },
    { path: `${iroot}history`, component: History },
    { path: `${iroot}webworkers`, component: WebWorkers },
    { path: `${iroot}draganddrop`, component: DragAndDrop },
    { path: `${iroot}file`, component: File },
    { path: `${iroot}geolocation`, component: Geolocation },
    { path: `${iroot}audio`, component: Audio },
    { path: `${iroot}video`, component: Video },
    { path: `${iroot}canvas2d`, component: Canvas2d },
    { path: `${iroot}cache`, component: Cache },
    { path: `${iroot}indexeddb`, component: IndexedDB },
    { path: `${iroot}*`, redirect: `${iroot}home` },
]

// 3.  router  `routes` 
// , 
const router = new VueRouter({
    // mode: 'history',
    routes //  routes: routes
})

export default router
